<template>
    <div class="login">
        <div class="out">
            <img class="img" src="../../assets/images/logo1.jpg" alt="">
            <div class="inner">
                <el-form :label-position="labelPosition" label-width="80px" :model="loginForm" ref="loginForm" :rules="loginRules" status-icon>
                    <el-form-item label="用户名" prop="username">
                        <el-input  v-model="loginForm.username" placeholder="请输入账号" clearable validate></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input v-model="loginForm.password" placeholder="请输入密码" show-password clearable validate></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="loginIn">登录</el-button>
                        <el-button @click="resetForm('loginForm')">重置</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
import { loginAxios } from '@/network/networds'
export default {
  data () {
    return {
      // 表单对齐方式
      labelPosition: 'right',
      loginForm: {
        username: '',
        password: ''
      },
      loginRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 5, message: '长度至少为8个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 登录
    loginIn () {
      this.$refs.loginForm.validate(value => {
        // 表单验证失败
        if (!value) {
          this.$message.error('错了哦，账号或密码格式不正确!')
        } else {
          // 表单验证成功
          loginAxios(this.loginForm).then(res => {
            if (res.data.code == '401') {
              this.$message.error(res.data.txt)
            } else if (res.data.code == '200') {
              window.sessionStorage.setItem('token', res.data.token)
              this.$router.push('/home')
              this.$message({
                message: '登录成功',
                type: 'success'
              })
            }
          })
        }
      })
    },
    // 重置表单
    resetForm (loginForm) {
      this.$refs[loginForm].resetFields()
    }
  }

}
</script>

<style lang="less" scope>
.login{
    height: 100%;
    background:#0D3F67;
    .out{
        background: white;
        width:460px;
        height:340px;
        position:relative;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);
        border:1px solid white;
        border-radius: 8px;
        .img{
            width:120px;
            height:120px;
            border-radius: 50%;
            position:absolute;
            top: -60px;
            left: 37%;
        }
        .inner{
            margin: 100px 60px 74px 15px;
        }
    }
}
</style>
